import './App.css';
import { Container, Navbar, Nav, Row, Col, Button } from 'react-bootstrap';
import { Routes, Route, useNavigate, Outlet } from 'react-router-dom';
import axios from 'axios';

import Detail from './route/Detail';
import Cart from './route/Cart';
import data from './data.js';
import nike from './nike';
import { useState } from 'react';

function App() {
  let [shoes, setShoes] = useState(data);
  let navigate = useNavigate();
  let [res1, setRes1] = useState([0,1,2,3,4,5,6,7,8]);
  let [nike1, setNike1] = useState(nike);
  let [count, setCount] = useState(1);

  return (
    <div className="App">
      <Navbar bg="dark" variant="dark">
        

        <Container>
          <Navbar.Brand onClick={() => navigate("/")}>ShoesShop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => navigate("/")}>Home</Nav.Link>
            <Nav.Link onClick={() => navigate("/detail/0")}>Detail</Nav.Link>
            <Nav.Link onClick={() => navigate("/cart")}>Cart</Nav.Link>
            <Nav.Link onClick={() => navigate("/about")}>About</Nav.Link>
          </Nav>
        </Container>
      </Navbar>

      {/* <Link to="/"></Link>
      <Link to="/detail"></Link>  */}
      
      <Routes>
        <Route path="/" element={
          <>
            <div className="slider"></div>

            <Title/>
            <Button variant="outline-primary" onClick={() => {
              let copy3 = [...shoes].sort((a, b) =>
                a.title > b.title ? 1 : -1,
              );

              setShoes(copy3);
              shoes = copy3;

              var res = [];
              for (let i in shoes) {
                res.push(shoes[i].id);
              }
              setRes1(res1 = res);
            }}> </Button>{' '}
            <Button variant="outline-secondary" onClick={() => {
              let copy4 = [...shoes].sort((a, b) =>
                a.price > b.price ? 1 : -1,
              );

              setShoes(copy4);
              shoes = copy4;
              var res = [];
              for (let i in shoes) {
                res.push(shoes[i].id);
              }
              setRes1(res1 = res);
            }}> </Button>{' '}
            <Button variant="outline-success" onClick={() => {
              let copy5 = [...shoes].sort((a, b) =>
                b.price > a.price ? 1 : -1,
              );
              setShoes(copy5);

              var res = [];
              for (let i in shoes) {
                res.push(shoes[i].id);
              }
              setRes1(res1 = res);
            }}> </Button>{' '}

            <Container>
              <Row>
                {
                  shoes.map((a, i) => {
                    return (
                      <Product shoes={shoes[i]} i={i} res1={res1} />
                    )
                  })
                }
              </Row>
            </Container>
            
            <Title2/>
            <Button variant="outline-success" count={count} onClick={() => {
              if (count === 1) {
                axios.get('https://jungwoonan.github.io/react_data/nike2.json').then((result) => {
                  let copy = [...nike, ...result.data];
                  setNike1(copy);
                  setCount(2);
                })
              } else if (count === 2) {
                axios.get('https://jungwoonan.github.io/react_data/nike3.json').then((result) => {
                  let copy = [...nike, ...result.data];
                  setNike1(copy);
                  setCount(3);
                })
              } else {
                alert("  .");
              }
            }}> + 3    </Button>{' '}
            <Container style={{ marginTop: '30px' }}>
              <Row>
                {
                  nike1.map((ele, i) => {
                    return (
                      <Nike nike1={nike1[i]} key={i}/>
                    )
                  })
                }
              </Row>
            </Container>  

            <Footer/>
          </>
        } />
        <Route path="/detail/:id" element={<Detail shoes={shoes}/>} />
        <Route path="/cart" element={<Cart/>} />
        <Route path="/about" element={<About />}>
          <Route path="member" element={<div></div>} />
          <Route path="location" element={<div></div>} />
        </Route>        
        <Route path="*" element={<div>.</div>} />
      </Routes>

    </div>
  );
}

