function Product(props) {
  let navigate = useNavigate();
  return (
    <Col md={4}>
      <Nav.Link onClick={() => {
        navigate('/detail/' + props.res1[props.i])
      }} className="c1">
        <img src={props.shoes.imgUrl} width="80%" alt="product"/>
        <h4>{props.shoes.title}</h4>
        <p>{props.shoes.price}</p>
      </Nav.Link>
    </Col>
  )
}

const Title = () => {
  let csst1 = {
    marginTop : "70px",
    textAlign : 'center'
  }

  return(
    <>
      <h3 style={csst1}>MD's Pick</h3>
      <p style={{textAlign:'center'}}>  ,  .</p>
    </>
  )
}

const Title2 =()=>{
  let csst2 = {
    marginTop:"70px",
  }
  return(
    <>
      <h3 style={csst2}>  </h3>
      <p>,       .</p>
    </>
  );
}

function Nike(props) {
  return (
    <Col md={4}>
        <img src={props.nike1.imgUrl} width="80%" alt="nike"/>
        <h5>{props.nike1.title}</h5>
        <span>{props.nike1.content}</span>
        <p>{props.nike1.price}</p>
    </Col>
  );
}

const Footer = ()=>{
  let foo ={
    color:"#fff",
    backgroundColor:"#000",
    padding:"20px 0px",
    marginTop:"80px",
    marginBottom:"0"
  }
  return(
    <>
      <p style={foo}>COPYRIGHT(C) 2023 Nike, Inc. All Rights Reserved</p>
    </>

  );
}

function About(){
  return(
   <>
     <h4>About</h4>
     <Outlet></Outlet>
   </>
  );
}

export default App;
