import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Container, Row, Col, Button, Nav, Alert } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { addItem } from '../store';

function Detail(props){
    let {id} = useParams();
    // console.log(id);
    // console.log(Number(id)+1);
    let [tab, setTab] = useState(0);
    let [alert, setAlert] = useState(true);
    useEffect(()=>{
        setTimeout(()=>{ setAlert(false) }, 2000)
    }, [])

    let [scale, setScale] = useState('')
    useEffect(()=>{
      setScale('end')
      return ()=>{
        setScale('')
      }
    },[])
    let dispatch = useDispatch()

    return(
        
        <Container className={"start " + scale}>
            {
                alert === true
                ? <Alert variant='warning'>
                    2  
                </Alert>
                :null
            }
            <Row>
                <Col md={6}>
                    <img src={'/img/shoes' + (Number(id)+1) + '.jpg'} width="100%" />
                </Col>
                <Col md={6}>
                    <h4 className="pt-5">{props.shoes[id].title}</h4>
                    <p>{props.shoes[id].content}</p>
                    <p>{props.shoes[id].price}</p>
                    <Button variant="danger" onClick={()=>
                      dispatch(addItem({id: props.shoes[id].id, name: props.shoes[id].title, count: 1}))
                    }></Button>
                </Col>
            </Row>

            <Nav variant="tabs" defaultActiveKey="link0">
                <Nav.Item>
                    <Nav.Link eventKey="link0" onClick={()=>{setTab(0)}}>0</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link1" onClick={()=>{setTab(1)}}>1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link2" onClick={()=>{setTab(2)}}>2</Nav.Link>
                </Nav.Item>
            </Nav>

            <TabContent tab={tab}/>           

        </Container>
    )
}

function TabContent({tab}){
    let [fade, setFade] = useState('')

    useEffect(()=>{
        setTimeout(()=>{setFade('fade_end')},50)
        return ()=>{
                setFade('');
            }
    }, [tab])
    return(
        <div className={"fade_start " + fade}>
            {[<div>0</div>, <div>1</div>, <div>2</div>][tab]}
        </div>
    )
}

export default Detail;